import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupBarStyle from './PickupBarStyle';
import {handleGeneralBarItemClick} from '../../../../../utilities/src/common_helper';

export default class PickupGoButton extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
      this.link.addEventListener("click", (e) => {
          handleGeneralBarItemClick(e, "pck_item_button");
      });
    }

    render() {
        const goImg = {
            margin: "6px 0 4px 0"
        };

        return (
            <div style={PickupBarStyle.div}>
                <a href={this.props.link} ref={c => this.link = c}>
                    <img src="/img/pickupbar_go_button.png" width="110" height="23" style={goImg}/>
                </a>
            </div>
        );
    }
}

PickupGoButton.propTypes = {
    link: PropTypes.string
}
