import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupBarStyle from './PickupBarStyle';
import {PICKUP_COUPON_PAGE_TYPE} from '../../../../../utilities/src/pickup_const'
import {EXTENSION_POPUP_SETTING_FAQ_LINK} from '../../../../../utilities/src/common_const'
import {handleGeneralBarItemClick} from '../../../../../utilities/src/common_helper';
import {validateRuntimeOnUserInteraction} from '../../../../../utilities/src/runtime_helper';

export default class PickupBarControls extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
      this.helpButton.addEventListener("click", (e) => {
          handleGeneralBarItemClick(e, (this.props.isCoupon) ? "pck_coupon_help_button"
                                                             : "pck_help_button");
      });

      this.closeButton.addEventListener("click", (e) => {
          e.preventDefault();
          validateRuntimeOnUserInteraction(() => {
            this.props.onClose();
          });
      });
    }

    render() {
        const liStyleCoupon = Object.assign({}, PickupBarStyle.li, {
            margin: "0",
            position: "absolute",
            right: "0px",
            top: "0px",
            backgroundColor: "#fff",
            padding: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"27px 10px":"22px 10px",
            display: "inline-block"
        });

        const liStyle = Object.assign({}, PickupBarStyle.li, {
            margin: "0",
            position: "absolute",
            right: "0px",
            top: "0px",
            backgroundColor: "#fff",
            padding: "16px 5px"
        });

        const closeImageStyle = {
            marginLeft: "16px"
        };

        return (
            <div style={this.props.isCoupon ? liStyleCoupon : liStyle}>
                <a href={EXTENSION_POPUP_SETTING_FAQ_LINK} ref={c => this.helpButton = c}>
                    <img src="/img/pickupbar_help.png" width="18" height="18"/>
                </a>
                <a href="#" ref={c => this.closeButton = c}>
                    <img src="/img/pickupbar_close.png" width="18" height="18" style={closeImageStyle}/>
                </a>
            </div>
        );
    }
}

PickupBarControls.propTypes = {
    onClose: PropTypes.func,
    isCoupon: PropTypes.bool,
    page_type: PropTypes.string
}
