import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupBarStyle from '../PickupBarStyle';
import {handleGeneralBarItemClick} from '../../../../../../utilities/src/common_helper';

export default class PickupRating extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
      this.link.addEventListener("click", (e) => {
          handleGeneralBarItemClick(e, "pck_item");
      });
    }

    render() {
        const spanStyle = Object.assign({}, PickupBarStyle.span, {
            color: "#ff8c00",
            margin: "0 16px",
            verticalAlign: "top"
        });

        const starsBackStyle = {
            position: "relative",
            color: "#e3e3e3",
            marginRight: "2px"
        };

        const ratingPercent = Math.floor(this.props.ratingData.ratingNumber / 5. * 100);

        let starsForeStyle = {
            position: "absolute",
            left: "0",
            width: ratingPercent + "%",
            whiteSpace: "nowrap",
            overflow: "hidden",
            color: "#ff8c00",
            marginRight: "2px"
        };

        const linkStyle = {
            textDecoration: "none",
            marginLeft: "2px",
            fontSize: "80%"
        };

        return (
            <span style={spanStyle}>
                <span style={starsBackStyle}>
                    ★★★★★
                    <span style={starsForeStyle}>★★★★★</span>
                </span>
                {this.props.ratingData.ratingNumber}
                <a href={this.props.reviewData.reviewUrl} style={linkStyle}
                   ref={c => this.link = c}>({this.props.reviewData.reviewCount.toLocaleString("ja").replace(',', ' ')}件)</a>
            </span>
        );
    }
}

PickupRating.propTypes = {
    ratingData: PropTypes.object,
    reviewData: PropTypes.object
}
