import React, { Component } from 'react';
import PropTypes from 'prop-types';

import {PICKUP_COUPON_TYPE, PICKUP_COUPON_PAGE_TYPE} from '../../../../../utilities/src/pickup_const'
import {handleGeneralBarItemClick} from '../../../../../utilities/src/common_helper';

export default class CouponHead extends Component {
    constructor(props) {
        super(props);
        this.formatDate = this.formatDate.bind(this);
    }

    componentDidMount() {
      this.link.addEventListener("click", (e) => {
          handleGeneralBarItemClick(e, "pck_coupon_ichiba");
      });
    }

    formatDate(_end_time) {
        const end_time = new Date(_end_time);
        let month   = end_time.getMonth() + 1,
            day     = end_time.getDate(),
            hour    = end_time.getHours(),
            minute  = end_time.getMinutes();
        return month + "/" + day + " " + hour + ":" + (minute+"").padStart(2, "0");
    }

    render() {
        const couponLogo = {
            display: "inline-block"
        }

        const keywordFont = {
            fontSize:"140%",
            maxWidth: "130px",
            textOverflow: "ellipsis",
            overflow: "hidden",
            whiteSpace: "nowrap",
            color: "#3f3f3f"
        }
        const coupon_message = {
            color: "#3f3f3f",
            fontSize: "70%"
          }
        const headerDiv = {
            display:"inline-block",
            height: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"72px":"62px"
        }
        const headerDivInner = {
            display:"inline-block",
            height: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"72px":"62px",
            verticalAlign: "top",
            color: "#3f3f3f"
        }
        const imageStyle = {
            width: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"125px":"100px",
            height: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"35px":"28px",
            margin: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"18px 5px":"16px 5px"
        }
        return (
            <div style={headerDiv}>
                <a href="https://www.rakuten.co.jp/?l-id=header_global_logo" target="_blank" ref={c => this.link = c} style={couponLogo} rel="noreferrer">
                    <img src="/img/pickupbar_logo.jpg" style={imageStyle} alt="Pickupbar Logo"/>
                </a>
                <div style={headerDivInner}>
                    <strong style={keywordFont} title={this.props.keyword}>{this.props.keyword}</strong>
                    {
                        this.props.coupon_type == PICKUP_COUPON_TYPE.COUPON_ADVANCE ?
                        <span><br />オススメクーポン情報<br /><small style={coupon_message}>有効期限 : {this.formatDate(this.props.items[0].end_time)} まで</small></span>
                        :<span><br />楽天市場ショップ<br />お買い得情報</span>
                    }
                </div>
            </div>
        );
    }
}

CouponHead.propTypes = {
    keyword: PropTypes.string,
    items: PropTypes.array,
    coupon_type: PropTypes.number,
    page_type: PropTypes.string
}
