import React, { Component } from "react";
import Slider from "react-slick";
import PropTypes from 'prop-types';
import CouponItem from './CouponItem';
import CouponItemRPP from './CouponItemRPP';

import {PICKUP_COUPON_TYPE, PICKUP_SLIDER_DISPLAY_ITEM_COUNT, PICKUP_COUPON_WIDTH, PICKUP_COUPON_NON_SLIDER_WIDTH} from '../../../../../utilities/src/pickup_const'
export default class MultipleItems extends Component {
    constructor(props) {
        super(props);
        this.items = this.props.items;
        this.state = {
          width: window.innerWidth
        };
    }
    componentDidMount() {
      window.addEventListener("resize", (e) => {
        this.setState({ width: window.innerWidth });//document.body.clientWidth
      });
    }

    render() {
      const availableWidth = this.state.width - PICKUP_COUPON_NON_SLIDER_WIDTH;
      let slidesToShow = PICKUP_SLIDER_DISPLAY_ITEM_COUNT;
      if(this.items.length<PICKUP_SLIDER_DISPLAY_ITEM_COUNT) {
        slidesToShow = this.items.length;
      }
      if(availableWidth / PICKUP_COUPON_WIDTH < slidesToShow) {
        slidesToShow = Math.max(Math.floor(availableWidth / PICKUP_COUPON_WIDTH), 1);
      }

      const settings = {
        arrows: true,
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: slidesToShow,
        slidesToScroll: slidesToShow
      };

      const containerStyle = {
        display: "inline-block",
        width: "100%",
        maxWidth: (slidesToShow*PICKUP_COUPON_WIDTH) + "px",
        marginLeft: "25px",
        textAlign: "left",
        marginTop: "2px"
      }

      return (
        <div style={containerStyle} id="coupon_slider">
              <Slider {...settings}>
                  {this.items.map(((item, i) => {
                      {
                        return this.props.coupon_type==PICKUP_COUPON_TYPE.COUPON_ADVANCE ?
                         <CouponItem key={i} item={item} page_type={this.props.page_type} />
                        : <CouponItemRPP key={i} item={item} page_type={this.props.page_type} />
                        }

                  }))}
              </Slider>
        </div>
      );
    }
  }

  MultipleItems.propTypes = {
    items: PropTypes.array,
    coupon_type: PropTypes.number,
    page_type: PropTypes.string,
}
