import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupBarStyle from '../PickupBarStyle';

export default class PickupSPU extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const goButton = Object.assign({}, PickupBarStyle.div, {
            verticalAlign: "initial",
            margin: "0 5px"
        });
        const spu01 = {
            fontSize: "85%",
            lineHeight: "15px",
            display: "inline-block"
        };

        const spu04 = {
            padding: "0px 10px",
            backgroundColor: "#bf0000",
            color: "#ffffff",
            borderRadius: "5px",
            display: "inline-block",
            lineHeight: "28px",
            position: "relative",
            top: "2px"
        };

        return (
            <div style={goButton}>
                <span style={spu01}>あなたは<br/>ポイント合計</span> <span style={spu04}>{this.props.spu}</span> 倍
            </div>
        );
    }
}

PickupSPU.propTypes = {
    spu: PropTypes.number
}
