import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PopupHelper from '../../../../../../utilities/src/popupHelper';
import PickupBarStyle from '../PickupBarStyle';
import PickupRating from './PickupRating';
import PickupPrice from './PickupPrice';
import {handleGeneralBarItemClick} from '../../../../../../utilities/src/common_helper';

export default class PickupProduct extends Component {
    constructor(props) {
        super(props);
        this.product = this.props.product;
        this.extensionHelper = new PopupHelper();
    }

    componentDidMount() {
      this.link.addEventListener("click", (e) => {
          handleGeneralBarItemClick(e, "pck_item");
      });
    }

    render() {
        const spanStyle = Object.assign({}, PickupBarStyle.span, {
            display: "inline-block",
            verticalAlign: "top",
            textDecoration: "none",
            fontSize: "120%",
            whiteSpace: "nowrap",
            maxWidth: "178px",
            overflow: "hidden",
            textOverflow: "ellipsis"
        });
        return (
            <div style={PickupBarStyle.div}>
                <a href={this.product.productUrl} ref={c => this.link = c}>
                    <img src={this.product.iconUrl} width="auto" height="35" style={PickupBarStyle.imgPickup}/>
                    <span style={spanStyle}>{this.product.productName}</span>
                </a>

                <PickupRating ratingData={this.product.rating} reviewData={this.product.review}/>

                <PickupPrice price={this.product.productPrice}
                             is_used_item={this.product.newProduct === false}/>
            </div>
        );
    }
}

PickupProduct.propTypes = {
    product: PropTypes.object
}
