import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupBarStyle from '../PickupBarStyle';

export default class PickupPoint extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const imgPickupPoint = Object.assign({}, PickupBarStyle.imgPickup, {
            verticalAlign: "middle"
        });
        const pointStyle = Object.assign({}, PickupBarStyle.span, {
            color: "#bf0000",
            fontWeight: "bold",
            marginLeft: "4px"
        });

        const pointBoxStyle = Object.assign({}, PickupBarStyle.div, {
            margin: "0 5px 0 10px"
        });

        return (
            <div className="pointbox" style={pointBoxStyle}>
                <span className="tooltip">ポイント数は、お会計時と異なる場合があります。</span>
                <img src="/img/pickupbar_point.png" style={imgPickupPoint}/>
                <span style={pointStyle}>{Math.floor(this.props.price * 0.01).toLocaleString("ja")}円相当～</span>
            </div>
        );
    }
}

PickupPoint.propTypes = {
    price: PropTypes.number
}