import React from 'react';
import {render} from 'react-dom';
import PickupBar from './components/pickupbar/PickupBar';

window.addEventListener('DOMContentLoaded', () => {
  window.parent.postMessage({ method: "RTBcs.PickupManager.getData" }, '*');
});

window.addEventListener('message', function(e) {
  var msg = e.data;
  if (msg.method == 'setData') {
    render(<PickupBar data={msg.value} clickClose={clickBarClose}/>,
           document.getElementById('bar'));
  }
});

var clickBarClose = () => {
  window.parent.postMessage({"method": "RTBcs.PickupManager.closeBar"}, '*');
};
