import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {PICKUP_COUPON_PAGE_TYPE} from '../../../../../utilities/src/pickup_const'

export default class CouponItem extends Component {
    constructor(props) {
        super(props);
        this.item = this.props.item;
    }

    render() {

      const slides = {
        fontSize: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"13px":"11px",
        color: "rgb(102, 101, 101)",
        backgroundColor: "rgb(255, 255, 255)",
        margin: "0px auto",
        position: "relative",
        left: "0px",
        right: "auto",
        top: "0px",
        width: "200px",
      }

      const coupon_price = {
        color: "#bf0000",
        fontSize: "160%",
        lineHeight: "150%"
      }

      const coupon_message = {
        "color": "rgb(191, 0, 0)",
        "fontSize": "120%",
        "lineHeight": "80%"
      }

      const coupon_line = {
        color: "#000",
        textDecoration: "line-through"
      }

      const coupon_red = {
        color: "#bf0000",
        fontSize: "100%",
        lineHeight: "80%"
      }

      const coupon_small = {
        "fontSize": "50%",
        "lineHeight": "170%"
      }

      const item_div = {
        height: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"65px":"55px",
        display: "inline-block",
        position: "relative",
        margin: "1px",
        padding: "0px 0px 0px 1px",
        border: "dotted 1px #999"
      }

      const img_div = {
        display: "inline-block",
        position: "absolute",
        width:this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"64px":"54px",
        height:this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"64px":"54px"
      }

      const desc_div = {
        display: "inline-block",
        position: "absolute",
        left: "75px",
        width: "142px",
        height: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"60px":"50px"
      }

    return (
        <div style={item_div}>
            <div style={slides}>
                <a href={this.item.redirect_url} target="_blank" ref={c => this.link = c} rel="noreferrer">
                    <img style={img_div}
                        src={this.item.item_image_url}
                        title={this.item.item_name}
                    />
                    <div style={desc_div}>
                        <div style={coupon_price}>
                            {this.item.discount_amount.toLocaleString("ja")}
                            <span style={coupon_small}>円</span>
                        </div>
                        <div style={coupon_message}><strong>OFFクーポン</strong></div>
                        <div>
                            <span style={coupon_line}>{this.item.item_price.toLocaleString("ja")}円</span>&nbsp;
                            <span style={coupon_red}>{(this.item.item_price - this.item.discount_amount).toLocaleString("ja")}円</span>
                        </div>
                    </div>
                </a>
            </div>
        </div>
        );
    }
  }

  CouponItem.propTypes = {
    item: PropTypes.object,
    page_type: PropTypes.string,
}
