import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupBarStyle from '../PickupBarStyle';

export default class PickupPrice extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const spanStyle = Object.assign({}, PickupBarStyle.span, {
            color: "#bf0000",
            fontWeight: "bold",
            fontSize: "110%",
            verticalAlign: "top",
            paddingTop: "5px"
        });

        return (
            <span style={spanStyle}>{this.props.is_used_item ? "中古" : ""}価格{this.props.price.toLocaleString("ja")}円(税込)～</span>
        );
    }
}

PickupPrice.propTypes = {
    price: PropTypes.number,
    is_used_item: PropTypes.bool
}
