import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupBanner from './PickupBanner';
import PickupBarControls from './PickupBarControls';
import CouponHead from './CouponHead';
import MultipleItems from './MultipleItems'
import {PICKUP_COUPON_PAGE_TYPE} from '../../../../../utilities/src/pickup_const'

export default class PickupBarCoupon extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const divCouponStyle = {
            width: "100%",
            overflow: "hidden",

            fontFamily: "'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'メイリオ', Meiryo, Osaka, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif",
            color: "#666565",
            backgroundColor: "#ffffff",
            paddingLeft: "0px",
            fontSize: this.props.data.matched_domain_details.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"13px":"11px"
        }
        return (
            <div style={divCouponStyle}>
                <PickupBanner bar_height={this.props.data.matched_domain_details.height} margin_adjustment={0} />
                <CouponHead keyword={this.props.data.keyword} items={this.props.data.items} coupon_type={this.props.data.coupon_type} page_type={this.props.data.matched_domain_details.page_type} />
                <MultipleItems items={this.props.data.items} coupon_type={this.props.data.coupon_type} page_type={this.props.data.matched_domain_details.page_type} />
                <PickupBarControls onClose={this.props.clickClose} isCoupon={true} page_type={this.props.data.matched_domain_details.page_type} />
            </div>
        );
    }
}

PickupBarCoupon.propTypes = {
    data: PropTypes.object,
    clickClose: PropTypes.func
}