import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupDisclaimerBanner from './PickupDisclaimerBanner';
import PickupLogo from './PickupLogo';
import PickupProduct from './product/PickupProduct';
import PickupPoint from './spu/PickupPoint';
import PickupSPU from './spu/PickupSPU';
import PickupGoButton from './PickupGoButton';

import PickupBarControls from './PickupBarControls';

export default class PickupBarPickup extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const divStyle = {
            height: "35px",
            maxWidth: "1350px",
            minWidth: "1150px",
            fontFamily: "'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro', 'メイリオ', Meiryo, Osaka, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif",
            fontSize: "13px",
            color: "#666565",
            margin: "6px auto"
        }

        return (
            <div style={divStyle}>
                <PickupLogo/>
                <PickupProduct product={this.props.data.product}/>
                <PickupPoint price={this.props.data.product.productPrice}/>
                <PickupSPU spu={this.props.data.spu.totalRate}/>
                <PickupGoButton link={this.props.data.product.productUrl}/>
                <PickupDisclaimerBanner/>
                <PickupBarControls onClose={this.props.clickClose} isCoupon={false}/>
            </div>
        );
    }
}

PickupBarPickup.propTypes = {
    data: PropTypes.object,
    clickClose: PropTypes.func
}