import React, { Component } from 'react';
import PickupBarStyle from './PickupBarStyle';
import {handleGeneralBarItemClick} from '../../../../../utilities/src/common_helper';

export default class PickupLogo extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
      this.link.addEventListener("click", (e) => {
          handleGeneralBarItemClick(e, "pck_ichiba");
      });
    }

    render() {
        return (
            <div style={PickupBarStyle.div}>
                <a href="https://www.rakuten.co.jp/?l-id=header_global_logo" target="_blank" ref={c => this.link = c} rel="noreferrer">
                    <img src="/img/pickupbar_logo.jpg" width="125" height="35" style={PickupBarStyle.imgPickup} alt="Pickupbar Logo"/>
                </a>
            </div>
        );
    }
}
