import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PickupBarCoupon from './PickupBarCoupon';
import PickupBarPickup from './PickupBarPickup';

export default class PickupBar extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <React.Fragment>
                {
                this.props.data.items != undefined ?
                    <PickupBarCoupon data={this.props.data} clickClose={this.props.clickClose}/>
                :
                    <PickupBarPickup data={this.props.data} clickClose={this.props.clickClose}/>
                }
            </React.Fragment>
        );
    }
}

PickupBar.propTypes = {
    data: PropTypes.object,
    clickClose: PropTypes.func
}