import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {PICKUP_COUPON_PAGE_TYPE} from '../../../../../utilities/src/pickup_const'

export default class CouponItemRPP extends Component {
    constructor(props) {
        super(props);
        this.item = this.props.item;
        this.item.map((_param) => {
            switch (_param.key) {
                case 'image':
                    this.image_url = _param.value;
                    this.redirect_url = _param.url;
                    break;
                case 'shop':
                    this.item_shop_name = _param.value;
                    break;
                case 'text':
                    this.item_name = _param.value[0]
                    break;
                case 'price':
                    this.item_price = parseInt(_param.value);
                    break;
                case 'image1':
                case 'image2':
                case 'image_origin':
                case 'point':
                case 'postageflg':
                case 'creditflg':
                case 'reviewnum':
                    break;
                default:
                    break;
            }
        });
    }

    render() {

      const slides = {
        fontSize: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"13px":"11px",
        color: "rgb(102, 101, 101)",
        backgroundColor: "rgb(255, 255, 255)",
        margin: "0px auto",
        position: "relative",
        left: "0px",
        right: "auto",
        top: "0px",
        width: "200px",
      }

      const shop_name = {
        color: "#333",
        fontSize: "85%",
        textAlign: "right",
        overflow: "hidden",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
        lineHeight: "100%",
        marginTop: "2px"
      }

      const offer_desc = {
        color: "#333",
        lineHeight: "130%",
        maxWidth: "130px",
        height: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"33px":"28px",
        padding: "3px 0 0 0",
        margin: "0px",
        textDecoration: "underline",
        overflow: "hidden"
      }

      const coupon_line = {
        color: "rgb(191, 0, 0)",
        lineHeight: "100%",
        textAlign: "left"
      }

      const item_div = {
        height: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"65px":"55px",
        display: "inline-block",
        position: "relative",
        margin: "0px",
        padding: "1px 0px 0px 1px",
        backgroundColor: "rgb(241,241,241)",
        border: "solid 1px #999",
        borderRadius: "6px"
      }

      const img_div = {
        display: "inline-block",
        position: "absolute",
        width:this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"64px":"54px",
        height:this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"64px":"54px"
      }

      const desc_div = {
        position: "absolute",
        left: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"75px":"65px",
        maxWidth: this.props.page_type==PICKUP_COUPON_PAGE_TYPE.SHOP?"125px":"135px"
      }

    return (
        <div style={item_div}>
            <div style={slides}>
                <a href={this.redirect_url} target="_blank" ref={c => this.imageLink = c} rel="noreferrer">
                  <img style={img_div}
                      src={this.image_url}
                      title={this.item_name}
                  />
                </a>
                <a href={this.redirect_url} target="_blank" ref={c => this.itemLink = c} rel="noreferrer">
                  <div style={desc_div}>
                      <div style={shop_name} title={this.item_shop_name}>
                          {this.item_shop_name}
                      </div>
                      <p style={offer_desc} title={this.item_name} className="offer_desc" name="offer_desc">{this.item_name}</p>
                      <div style={coupon_line}>{ this.item_price.toLocaleString("ja")}円</div>
                  </div>
                </a>
            </div>
        </div>
        );
    }
  }

CouponItemRPP.propTypes = {
    item: PropTypes.array,
    page_type: PropTypes.string,
}
